import { DatePickerProps } from '@material-ui/pickers'
import { useXFieldForInput, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { InputDate } from '../../atoms/InputDate'

interface IProps extends LegacyOmit<DatePickerProps, 'value' | 'onChange'> {
  fieldName: XFieldName
}

export const FormInputDate: React.FC<IProps> = ({
  fieldName,
  ...pickerProps
}) => {
  const [field, getProps] = useXFieldForInput(fieldName)
  return useObserver(() => (
    <InputDate
      {...getProps()}
      onChange={field.setValue}
      onOpen={field.setTouched}
      {...pickerProps}
    />
  ))
}
